
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
      
import { setTimeout } from 'timers';

export default {
name:'login',
components:{                         //注册组件
Group,
XInput,
XButton,
XImg,
},
data(){
return{
account:'',
pawd:'',
logSrc: require('./../../assets/image/logo1.png'),
}
},
created(){
this.$store.commit('keepAlive','login')
},
methods:{
tj(){


if(this.account!='' && this.pawd!=''){
this.$vux.loading.show({}) 
this.$axios({
headers: {
"Content-Type": "application/x-www-form-urlencoded ",
},
method: 'POST',
dataType: 'json',
url: this.url+'/userServiceController.do?applogin',   //请求的接口地址
data: {
userName :this.account,
passWord :this.pawd
},
transformRequest: [function (data) {
let ret = ''
for (let it in data) {
ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
}
return ret
}],
}).then((res)=>{
this.$vux.loading.hide() 
if(res.data.msgCode=='0001'){
localStorage.sessionid=res.data.sessionid
sessionStorage.logined =true
this.$store.commit('logined',true)
this.$router.push({path:'/'})
}else{
this.$vux.toast.show({
time:'2000',
type:'cancel',
text: '用户名或密码错误'
})
}
}).catch((err)=>{

this.$vux.loading.hide() 
this.$vux.alert.show({
title: '注意',
content: '服务器异常,请稍后再试!',
})
})
}else{
this.$vux.toast.show({
time:'2000',
type:'warn',
text: '用户名和密码不能为空'
})
}

}
},
beforeRouteLeave (to, from, next) {
if(to.path == '/'){
this.$store.commit('changekeepAlive','enter')
}
next()
},
}



